import React, { useEffect, useState } from 'react';
import { supabase } from '../utils/supabaseClient';

const AddProductInventoryForm = ({ onProductAdded, codigoInicial = '' }) => {
  const [product, setProduct] = useState({
    name: '',
    barcode: codigoInicial,
    quantity: 1
  });

  const [loadingCatalog, setLoadingCatalog] = useState(false);
  const [productoEnCatalogo, setProductoEnCatalogo] = useState(false);

  // Buscar el producto en catalogo_productos si hay un c√≥digo escaneado
  useEffect(() => {
    const buscarEnCatalogo = async () => {
      if (!codigoInicial) return;

      setLoadingCatalog(true);
      const { data, error } = await supabase
        .from('catalogo_productos')
        .select('nombre')
        .eq('codigo_barras', codigoInicial)
        .single();

      if (error) {
        console.log('üîç Producto no encontrado en cat√°logo. Se puede a√±adir manualmente.');
        setProductoEnCatalogo(false);
        setProduct(prev => ({
          ...prev,
          barcode: codigoInicial,
          name: ''
        }));
      } else {
        console.log('‚úÖ Producto encontrado en cat√°logo:', data.nombre);
        setProductoEnCatalogo(true);
        setProduct(prev => ({
          ...prev,
          barcode: codigoInicial,
          name: data.nombre
        }));
      }

      setLoadingCatalog(false);
    };

    buscarEnCatalogo();
  }, [codigoInicial]);

  const handleChange = (e) => {
    const { name, value } = e.target;
    setProduct(prev => ({
      ...prev,
      [name]: name === 'quantity' ? parseInt(value) : value
    }));
  };

  const handleSubmit = async (e) => {
    e.preventDefault();

    const productoParaGuardar = {
      nombre: product.name,
      codigo_barras: product.barcode,
      cantidad: product.quantity
    };

    // 1. Guardar en lista_compra
    const { error: errorLista } = await supabase.from('lista_compra').insert([productoParaGuardar]);

    if (errorLista) {
      console.error('‚ùå Error al guardar en lista_compra:', errorLista.message);
      alert('‚ùå Error al guardar el producto en la lista');
      return;
    }

    // 2. Si el producto no estaba en cat√°logo, guardarlo all√≠ tambi√©n
    if (!productoEnCatalogo) {
      const { error: errorCatalogo } = await supabase.from('catalogo_productos').insert([
        {
          nombre: product.name,
          codigo_barras: product.barcode
        }
      ]);

      if (errorCatalogo) {
        console.error('‚ö†Ô∏è Error al guardar en cat√°logo:', errorCatalogo.message);
        alert('‚ö†Ô∏è El producto se guard√≥ en la lista, pero no en el cat√°logo.');
      } else {
        console.log('‚úÖ Producto tambi√©n a√±adido al cat√°logo');
      }
    }

    if (onProductAdded) onProductAdded();
    setProduct({ name: '', barcode: '', quantity: 1 });
  };

  return (
    <form onSubmit={handleSubmit} className="space-y-4">
      {loadingCatalog && <p className="text-sm text-gray-500">Buscando en el cat√°logo...</p>}

      <div>
        <label className="block text-sm font-medium text-gray-700">Nombre</label>
        <input
          type="text"
          name="name"
          value={product.name}
          onChange={handleChange}
          required
          className="mt-1 block w-full border border-gray-300 rounded-md shadow-sm py-2 px-3"
          placeholder="Nombre del producto"
        />
      </div>

      <div>
        <label className="block text-sm font-medium text-gray-700">C√≥digo de Barras</label>
        <input
          type="text"
          name="barcode"
          value={product.barcode}
          onChange={handleChange}
          required
          className="mt-1 block w-full border border-gray-300 rounded-md shadow-sm py-2 px-3"
          placeholder="Ej: 8431234567890"
        />
      </div>

      <div>
        <label className="block text-sm font-medium text-gray-700">Cantidad</label>
        <input
          type="number"
          name="quantity"
          min="1"
          value={product.quantity}
          onChange={handleChange}
          className="mt-1 block w-full border border-gray-300 rounded-md shadow-sm py-2 px-3"
        />
      </div>

      <button
        type="submit"
        className="w-full bg-green-600 text-white py-2 px-4 rounded-md hover:bg-green-700"
      >
        Agregar Producto
      </button>
    </form>
  );
};

export default AddProductInventoryForm;
