import React, { useState, useEffect } from 'react';
import { supabase } from '../utils/supabaseClient';
import { useSupabaseData } from '../hook/useSupabaseData';

const AddProductForm = ({ onProductAdded, Inventario = null }) => {
  const [product, setProduct] = useState(Inventario || {
    name: '',
    barcode: '',
    quantity: 1,
    category: '',
    location: '',
    expiryDate: ''
  });

  const { data: categorias } = useSupabaseData('categorias');
  const { data: ubicaciones } = useSupabaseData('ubicaciones');

  // Buscar producto existente por c√≥digo de barras
  useEffect(() => {
    const buscarProductoPorCodigo = async () => {
      if (!product.barcode) return;

      const { data, error } = await supabase
        .from('catalogo_productos')
        .select('*')
        .eq('codigo_barras', product.barcode)
        .limit(1);

      if (error) {
        console.error('Error buscando por c√≥digo de barras:', error.message);
        return;
      }

      if (data.length > 0) {
        const existente = data[0];
        setProduct(prev => ({
          ...prev,
          name: existente.nombre,
          category: existente.categoria_id,
          location: existente.ubicacion_id,
          expiryDate: existente.fecha_caducidad
        }));
      }
    };

    buscarProductoPorCodigo();
  }, [product.barcode]);

  const handleChange = (e) => {
    const { name, value } = e.target;
    setProduct(prev => ({ ...prev, [name]: name === 'quantity' ? parseInt(value) : value }));
  };

  const handleSubmit = async (e) => {
    e.preventDefault();

    const productoParaGuardar = {
      nombre: product.name,
      codigo_barras: product.barcode,
      fecha_caducidad: product.expiryDate,
      categoria_id: product.category,
      ubicacion_id: product.location,
      cantidad: product.quantity
    };

    // Insertar en cat√°logo si no existe
    const { data: productoCatalogo, error: errorCatalogo } = await supabase
      .from('catalogo_productos')
      .select('*')
      .eq('codigo_barras', productoParaGuardar.codigo_barras)
      .limit(1);

    if (errorCatalogo) {
      console.error('‚ùå Error consultando cat√°logo:', errorCatalogo.message);
    }

    if (!productoCatalogo || productoCatalogo.length === 0) {
      const { error: errorInsertarCatalogo } = await supabase
        .from('catalogo_productos')
        .insert([{
          nombre: productoParaGuardar.nombre,
          codigo_barras: productoParaGuardar.codigo_barras,
          //categoria_id: productoParaGuardar.categoria_id
        }]);

      if (errorInsertarCatalogo) {
        console.error('‚ùå Error al insertar en cat√°logo:', errorInsertarCatalogo.message);
        alert('‚ùå No se pudo guardar el producto en el cat√°logo');
        return;
      }
    }

    // Verificar si ya existe el producto con el mismo nombre, c√≥digo y fecha
    const { data: existentes, error: buscarError } = await supabase
      .from('productos')
      .select('*')
      .eq('nombre', productoParaGuardar.nombre)
      .eq('codigo_barras', productoParaGuardar.codigo_barras)
      .eq('fecha_caducidad', productoParaGuardar.fecha_caducidad)
      .limit(1);

    if (buscarError) {
      console.error('‚ùå Error buscando producto existente:', buscarError.message);
      alert('‚ùå Error al buscar producto existente');
      return;
    }

    if (existentes.length > 0) {
      // Ya existe ‚Üí sumamos cantidad
      const productoExistente = existentes[0];
      const nuevaCantidad = productoExistente.cantidad + productoParaGuardar.cantidad;

      const { error: updateError } = await supabase
        .from('productos')
        .update({ cantidad: nuevaCantidad })
        .eq('id', productoExistente.id);

      if (updateError) {
        console.error('‚ùå Error al actualizar cantidad:', updateError.message);
        alert('‚ùå Error al actualizar el producto');
        return;
      }

      alert('‚úÖ Producto actualizado correctamente');
    } else {
      // No existe ‚Üí insertamos
      const { error: insertError } = await supabase
        .from('productos')
        .insert([productoParaGuardar]);

      if (insertError) {
        console.error('‚ùå Error al insertar:', insertError.message);
        alert('‚ùå Error al guardar el producto');
        return;
      }

      alert('‚úÖ Producto insertado correctamente');
    }

    if (onProductAdded) onProductAdded();

    setProduct({
      name: '',
      barcode: '',
      quantity: 1,
      category: '',
      location: '',
      expiryDate: ''
    });
  };

  return (
    <form onSubmit={handleSubmit} className="space-y-4">
      <div>
        <label className="block text-sm font-medium text-gray-700">Nombre</label>
        <input
          type="text"
          name="name"
          value={product.name}
          onChange={handleChange}
          required
          className="mt-1 block w-full border border-gray-300 rounded-md shadow-sm py-2 px-3"
        />
      </div>

      <div>
        <label className="block text-sm font-medium text-gray-700">C√≥digo de Barras</label>
        <input
          type="text"
          name="barcode"
          value={product.barcode}
          onChange={handleChange}
          className="mt-1 block w-full border border-gray-300 rounded-md shadow-sm py-2 px-3"
        />
      </div>

      <div>
        <label className="block text-sm font-medium text-gray-700">Cantidad</label>
        <input
          type="number"
          name="quantity"
          min="1"
          value={product.quantity}
          onChange={handleChange}
          className="mt-1 block w-full border border-gray-300 rounded-md shadow-sm py-2 px-3"
        />
      </div>

      <div>
        <label className="block text-sm font-medium text-gray-700">Categor√≠a</label>
        <select
          name="category"
          value={product.category}
          onChange={handleChange}
          className="mt-1 block w-full border border-gray-300 rounded-md shadow-sm py-2 px-3"
        >
          <option value="">Seleccione una categor√≠a</option>
          {categorias.map((cat) => (
            <option key={cat.id} value={cat.id}>{cat.nombre}</option>
          ))}
        </select>
      </div>

      <div>
        <label className="block text-sm font-medium text-gray-700">Ubicaci√≥n</label>
        <select
          name="location"
          value={product.location}
          onChange={handleChange}
          className="mt-1 block w-full border border-gray-300 rounded-md shadow-sm py-2 px-3"
        >
          <option value="">Seleccione una ubicaci√≥n</option>
          {ubicaciones.map((ubi) => (
            <option key={ubi.id} value={ubi.id}>{ubi.nombre}</option>
          ))}
        </select>
      </div>

      <div>
        <label className="block text-sm font-medium text-gray-700">Fecha de Caducidad</label>
        <input
          type="date"
          name="expiryDate"
          value={product.expiryDate}
          onChange={handleChange}
          className="mt-1 block w-full border border-gray-300 rounded-md shadow-sm py-2 px-3"
        />
      </div>

      <button
        type="submit"
        className="w-full bg-green-600 text-white py-2 px-4 rounded-md hover:bg-green-700"
      >
        {Inventario ? 'Actualizar Producto' : 'Agregar Producto'}
      </button>
    </form>
  );
};

export default AddProductForm;
