import React from 'react';
import { useListaCompra } from '../hook/useListaCompra';
//import { supabase } from '../utils/supabaseClient';

const ShoppingListItem = ({product, onTogglePurchased}) => {
  const { lista, loading } = useListaCompra();

  if (loading) return <p>Cargando lista de la compra...</p>;
  if (lista.length === 0) return <p>No hay productos en tu cesta.</p>;
  return (
       
    <div className="bg-white shadow rounded-lg p-4 mb-4 border-l-4 border-green-500">
      <div className="flex justify-between items-start">
        <div>
          <h3 className="font-medium text-lg">{product.nombre}</h3>
          <p className="text-gray-600">Cantidad: {product.cantidad}</p>
        </div>
      </div>
      <div className="mt-4 flex space-x-2">
        <button
          onClick={() => onTogglePurchased(product)}
          className="text-sm bg-green-500 text-white py-1 px-3 rounded hover:bg-green-600"
        >
          Comprado
        </button>
      </div>
    </div>
  );
};

export default ShoppingListItem;
